@import './dark.css'; // css do flexbox cung cấp


.flexlayout__tab_button:hover .flexlayout__tab_button_trailing,
.flexlayout__tab_button--selected .flexlayout__tab_button_trailing, .flexlayout__tab_button_trailing {
  background: transparent url("../../conponents/translate/icon/close.png")
    no-repeat center;
    background-size: 12px;
    width: 12px;
    height: 12px;
}
.flexlayout__layout {
  display: flex;
  flex-grow: 1;
  -webkit-flex-grow: 1;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  position: absolute;
}
.contents .flexlayout__layout {
  display: flex;
  flex-grow: 1;
  -webkit-flex-grow: 1;
  left: 40px;
  top: 87px;
  right: 0;
  bottom: 0;
  position: absolute;
  height: calc(100% - 88px);
}
.flexlayout__layout {
  background-color: var(--main__background__color);
  color: var(--main__content__color);
  .flexlayout__tab {
    background-color: var(--main__background__color);
    color: var(--main__content__color);
    padding: 5px;
    overflow: hidden;
  }
  .flexlayout__tab_button--selected {
    color: var(--secondary__content__color);
    background-color: var(--main__background__color);
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
  }
  svg > rect {
    fill: var(--third__table__background) !important;
  }
  .flexlayout__splitter {
    background: var(--secondary__background__color);
    opacity: 1;
    transition: opacity 0.2s ease;
  }
  .flexlayout__tab_button {
    margin-left: 0px;
  }
  .flexlayout__tab_header_outer {
    background-color: var(--flexlayout__background);
  }
  .flexlayout__tabset_header_outer {
    background-color: var(--flexlayout__background);
  }
  .flexlayout__tabset-selected {
    background-image: linear-gradient(
      180deg,
      var(--flexlayout__select),
      var(--flexlayout__maximize)
    );
  }
  .flexlayout__tabset-maximized {
    background-image: linear-gradient(
      45deg,
      var(--flexlayout__maximize),
      var(--flexlayout__maximize)
    );
  }
  .flexlayout__tab_button_content {
    font-size: 1.1rem;
    // font-weight: bold;
  }
  .flexlayout__tabset {
    background-color: unset;
  }
  .flexlayout__tab_button,
  .flexlayout__tab_button--selected {
    margin-top: 0px !important;
  }
}
.flexlayout__drag_rect {
  color: #273040;
  width: 200px;
  height: 150px;
  background: #d8d8d8e6;
}