::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  background-color: var(--component__deep_gray);
  border-radius: 6px;
}
::-webkit-scrollbar-track {
//   -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: var(--scrollbar__track);
  border-radius: 6px;
}
::-webkit-scrollbar-thumb {
//   -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: var(--scrollbar__thumb);
  border-radius: 6px;
}
