body {
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-size: 14px;
  font-family: Arial, sans-serif;
}
#root {
  left: 1px;
  top: 1px;
  right: 1px;
  bottom: 1px;
  position: absolute;
  display: flex;
  display: -webkit-flex;
  background-color: var(--secondary__background__color);
}
